<template>
    <input type="text" class="form-control" :value="value">
</template>
<script type="text/javascript">
  import Pickaday from 'pikaday'

  export default {
    props: ['value', 'format', 'name'],
    data() {
      return {
        picker: null,
        inputName: this.name || 'date'
      }
    },
    watch: {
      value(value){
        this.picker.setDate(value)
      }
    },
    mounted() {
      const getDate = this.getDate
      this.picker = new Pickaday({
        field: this.$el,
        bound: true,
        defaultDate: this.value,
        format: 'YYYY-MM-DD',
        onSelect: function (date) {
          getDate(this.getMoment(date).format('YYYY-MM-DD'))
        }
      })
      this.picker.setDate(this.value)
    },
    methods: {
      getDate(date) {
        this.$emit(this.inputName, {date, field: this.inputName})
        this.$emit('input', date)
      }
    }
  }
</script>
<style lang="scss">
    @import "../sass/styles";
    @import '~pikaday/scss/pikaday';

    .pika-lendar {
        button {
            &.pika-next {
                background-image: url(data:image/svg+xml;utf8;base64,);
            }
            &.pika-prev {
                background-image: url(data:image/svg+xml;utf8;base64,);
            }
        }
        table.pika-table {
            th abbr {
                text-decoration: none;
            }
            .pika-row td {
                border: solid 1px $tabbs-gray-4;
                &.is-today {
                    background: $blue-4;
                    color: $white
                }
                &.is-selected {
                    background: $tabbs-pink;
                    color: $white;
                    .pika-button {
                        box-shadow: none;
                        border-radius: 0;
                    }
                }
                .pika-button {
                    text-align: center;
                    color: inherit;
                    background: inherit;
                }
            }
        }
    }
</style>



